import {
	mapState,
	mapMutations
} from 'vuex'
export default {
	computed: {
		...mapState(['gender', 'addressList'])
	},
	data() {
		return {
			sexChecked: true,
			tag: ['公司', '家', '学校'],
			activeIndex: -1,
			addressData: {
				linkman: "",
				phone: "",
				address: "",
				tower: ""
			},
			editData: {},
			isDisabled: false,
			j: 0
		}
	},
	onLoad(e) {
		this.j = e.data;
		var d = this.addressList[e.data];
		this.editData = d;
		this.editData.sex == "先生" ? this.sexChecked = true : this.sexChecked = false;
		var at = this.editData.addressType;
		if (at == "公司") {
			this.activeIndex = 0;
		} else if (at == "家") {
			this.activeIndex = 1;
		} else if (at == "学校") {
			this.activeIndex = 2;
		}
		this.addressData = {
			linkman: d.linkman,
			phone: d.phone,
			address: d.address,
			tower: d.tower
		}
	},
	methods: {
		...mapMutations(['editAddress', 'removeAddress']),
		tagChange(index) {
			this.activeIndex = index;
		},
		formSubmit(e) {
			var _this = this;
			var i = this.activeIndex;
			var d = e.detail.value;
			var thisTag;
			i == -1 ? thisTag = "" : thisTag = _this.tag[i];
			this.editData = {
				linkman: d.linkman,
				sex: d.sex,
				phone: d.phone,
				address: d.address,
				addressType: thisTag,
				isDefault: d.default,
				tower: d.tower
			}
			var obj = {
				num: _this.j,
				d: _this.editData
			}
			this.editAddress(obj);
			uni.navigateBack();
		},
		input(e) {
			var id = e.currentTarget.id;
			var ad = this.addressData;
			var _this = this;
			for (var key in ad) {
				key == id ? ad[key] = e.detail.value : ad[key];
				if (ad[key] !== "") {
					_this.isDisabled = false
				}
				for (var k in ad) {
					if (ad[k] == "") {
						_this.isDisabled = true
					}
				}
			}
		}
	},
	onNavigationBarButtonTap(e) {
		var _this = this;
		uni.showModal({
			title: '提示',
			content: '确认删除？',
			success: function(res) {
				if (res.confirm) {
					_this.removeAddress(_this.j);
					uni.navigateBack();
				} else if (res.cancel) {

				}
			}
		});

	}
}
